@import url(https://fonts.googleapis.com/css?family=Darker+Grotesque:900&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html, body {
  height: 100vh;
  width: 100vw;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

.flip-root{
  height: 300px;
  width: 100vw;
}
.card_list {
  background-color: #212121;
  height: 100vh;
  width: 100vw;
}

body {
  background: #212121;
  color: white;
  font-size: 21px;
  font-family: 'Darker Grotesque'
}

.form-control, .form-control:hover, .form-control:focus, .btn, .btn:hover, .btn:focus {
  color: white;
  background: #212121;
  border: 1px solid white;
  margin: 5px;
  outline: 0;
}
